
html, body {
  margin: 0;
  padding: 0;
  background: #faf8ef;
  color: #776e65;
  font-family: "Clear Sans", "Helvetica Neue", Arial, sans-serif;
  font-size: 18px;
}
#app {
  width: 500px;
  margin: 50px auto;
}
.heading {
  margin-bottom: 15px;
}
.heading:after {
    content: "";
    display: block;
    clear: both;
}
h1.title {
  font-size: 80px;
  font-weight: bold;
  margin: 0;
  display: block;
  float: left;
}
.scores-container {
  float: right;
  text-align: right;
  position: relative;
}
.score-container, .best-container {
  position: relative;
  display: inline-block;
  background: #bbada0;
  padding: 15px 25px;
  font-size: 25px;
  height: 25px;
  line-height: 47px;
  font-weight: bold;
  border-radius: 3px;
  color: white;
  margin-top: 8px;
  text-align: center;
}
.score-container:after {
  content: "Score";
}
.best-container:after {
  content: "Best";
}
.score-container:after, .best-container:after {
  position: absolute;
  width: 100%;
  top: 10px;
  left: 0;
  text-transform: uppercase;
  font-size: 13px;
  line-height: 13px;
  text-align: center;
  color: #eee4da;
}
.score-add {
  position: absolute;
  color: rgba(119, 110, 101, 0.9);
  font-size: 25px;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 10;
  opacity: 0;
  -webkit-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
}
.restart-container {
  line-height: 2.45;
}
.restart-container:after {
    content: "";
    display: block;
    clear: both;
}
.restart-container .tip {
    width: 350px;
    float: left;
}
.btn {
  background: #8f7a66;
  border-radius: 3px;
  padding: 0 20px;
  text-decoration: none;
  color: #f9f6f2;
  height: 40px;
  line-height: 42px;
  text-align: center;
  border: 0;
  cursor: pointer;
  outline: none;
  font-size: inherit;
  font-weight: 600;
}
.restart-button {
  float: right;
}
.game-container {
  width: 500px;
  height: 500px;
  padding: 15px;
  margin-top: 40px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-radius: 6px;
  background: #bbada0;
  position: relative;
}
.grid-container {
  position: absolute;
  z-index: 1;
}
.grid-row {
  margin-bottom: 15px;
}
.grid-row:after {
    content: "";
    display: block;
    clear: both;
}
.grid-row:last-child {
    margin-bottom: 0;
}
.grid-cell {
  width: 106.25px;
  height: 106.25px;
  border-radius: 3px;
  float: left;
  margin-right: 15px;
  background: rgba(238, 228, 218, 0.35);
}
.grid-cell:last-child {
    margin-right: 0;
}
.tile-container {
  position: absolute;
  z-index: 2;
}
