.tile-new .tile-inner {
  -webkit-animation: appear 200ms ease 100ms;
          animation: appear 200ms ease 100ms;
  -webkit-animation-fill-mode: backwards;
          animation-fill-mode: backwards;
}
.tile-merged .tile-inner {
  z-index: 20;
  -webkit-animation: pop 200ms ease-in-out 100ms;
          animation: pop 200ms ease-in-out 100ms;
  -webkit-animation-fill-mode: backwards;
          animation-fill-mode: backwards;
}
.message-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-align: center;
  z-index: 100;
  -webkit-animation: fade-in 800ms ease-in-out both;
          animation: fade-in 800ms ease-in-out both;
}
.message-container p {
    font-size: 60px;
    font-weight: bold;
    height: 60px;
    line-height: 60px;
    margin-top: 180px;
    margin-bottom: 25px;
}
.gg {
  background: rgba(238, 228, 218, 0.5);
}
.win {
  background: rgba(237, 194, 46, 0.5);
}
footer {
  margin-top: 40px;
}
footer hr {
    border: none;
    border-bottom: 1px solid #d8d4d0;
    margin-top: 20px;
    margin-bottom: 30px;
}
footer a {
    color: #776e65;
}
@-webkit-keyframes appear {
from {
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
}
to {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes appear {
from {
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
}
to {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@-webkit-keyframes pop {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes pop {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@-webkit-keyframes fade-in {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes fade-in {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
